.home{
    /* display: flex; */
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    max-width: 1200px;
}
.home__row{
    display: flex;
    z-index: 1;
    margin-left: 5px;
    margin-right: 5px;
}

.home__image{
    width: 100%;
    z-index: -1;
    margin-bottom: -180px;
    mask-image: linear-gradient(to bottom, rgba(0,0,0,2),rgba(0,0,0,0));
}